* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@media ( max-width: 1080px ) {
  html {
    font-size: 93.75%;
  }

}

@media ( max-width: 720px ) {
  html {
    font-size: 87.5%;
  }

}

body {
  background-color: #333;
}

body,
input,
textarea,
button {
  font: 400 1rem sans-serif;
}

button {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}
